import { forwardRef, ReactNode } from "react";
import classNames from "classnames/bind";

import { CountryVersion } from "common/constants/enum";

import { ButtonProps, ButtonSize, ButtonTheme } from "./Button.types";
import { Content } from "./Content";
import styles from "./Button.module.scss";

const cx = classNames.bind(styles);

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
    (props: ButtonProps, ref) => {
        const {
            className,
            disabled = false,
            id,
            href,
            link,
            name,
            isLoading = false,
            onClick,
            size = ButtonSize.S,
            theme = ButtonTheme.RED,
            type = "button",
            children,
            target,
            icon,
            isMaxWidth,
            version,
        } = props;

        let content: string | ReactNode = name;
        const linkButton: string | undefined = href || link;

        const classList = cx(
            "root",
            `size_${size}`,
            `theme_${theme}`,
            {
                [`theme_${theme}_disabled`]: disabled && !isLoading,
                "root--max-width": isMaxWidth,
                "root-with-icon": icon,
                "root_version-germany": version === CountryVersion.GERMANY,
            },
            className
        );

        if (children) {
            content = children;
        }

        if (linkButton) {
            return (
                <a className={classList} href={linkButton} target={target}>
                    <Content icon={icon} content={content} />
                </a>
            );
        }

        return (
            <button
                onClick={onClick}
                id={id}
                type={type}
                ref={ref}
                className={classList}
                disabled={disabled || isLoading}
            >
                <Content
                    icon={icon}
                    theme={theme}
                    isLoading={isLoading}
                    content={content}
                />
            </button>
        );
    }
);
