import { useRouter } from "next/router";

import { WINDOW_SIZE, COOKIES } from "common/constants/enum";

import { getCookiesValueByName } from "./cookie";

type GetAnalyticsIdOptions = {
    zone: string;
    element?: GetAnalyticsIdElement;
    label?: number | string;
};

export enum GetAnalyticsIdElement {
    BUTTON = "button",
    LINK = "link",
}

enum VIEW {
    PHONE = "phone",
    TABLET = "tablet",
    DESKTOP = "desktop",
}

const NONE = "none";

export const getAnalyticsId = (options: GetAnalyticsIdOptions) => {
    const {
        zone,
        element = GetAnalyticsIdElement.BUTTON,
        label = NONE,
    } = options;

    let view;
    let experiments;

    const router = useRouter();

    const pathname = router?.asPath
        ?.split("?")[0]
        .split("/")
        .filter(Boolean)
        .join("-");

    const hasWindow = typeof window !== "undefined";
    const width = hasWindow ? window.innerWidth : 0;

    if (width < WINDOW_SIZE.TABLET) {
        view = VIEW.PHONE;
    } else if (width < WINDOW_SIZE.DESKTOP) {
        view = VIEW.TABLET;
    } else {
        view = VIEW.DESKTOP;
    }

    try {
        const experimentCookieValue = getCookiesValueByName(
            COOKIES.EXPERIMENTS
        );

        experiments = experimentCookieValue
            ? `${COOKIES.EXPERIMENTS}-${experimentCookieValue
                  .split(",")
                  .join("-")}`
            : NONE;
    } catch (ex) {
        experiments = NONE;
    }

    return [pathname, zone, element, view, label, experiments].join("_");
};
