import { FC } from "react";
import classNames from "classnames/bind";

import { isStringWithHtml } from "common/utils/containsHtml";

import {
    HeadingTag,
    HeadingTheme,
    HeadingProps,
    HeadingType,
} from "./Heading.types";
import styles from "./Heading.module.scss";

const cx = classNames.bind(styles);

export const Heading: FC<HeadingProps> = ({
    children,
    className,
    tag: CurrentHeadingTag = HeadingTag.H1,
    type = HeadingType.XL,
    theme = HeadingTheme.Default,
}) => {
    const classList = cx("root", `theme_${theme}`, `type_${type}`, className);

    return isStringWithHtml(children) ? (
        <CurrentHeadingTag
            className={classList}
            dangerouslySetInnerHTML={{ __html: children as string }}
        />
    ) : (
        <CurrentHeadingTag className={classList}>{children}</CurrentHeadingTag>
    );
};
