import React, { FC } from "react";
import classNames from "classnames/bind";

import { LoadingIcon } from "common/components/Icons/LoadingIcon";
import { Icon } from "common/components/Icons";

import styles from "./Button.module.scss";
import {
    ButtonTheme,
    mapThemeColorLoading,
    ContentProps,
} from "./Button.types";

const cx = classNames.bind(styles);

export const Content: FC<ContentProps> = ({
    isLoading,
    content,
    theme,
    icon,
}: ContentProps) => {
    const loadingIcon = isLoading && (
        <LoadingIcon
            stroke={mapThemeColorLoading[theme || ButtonTheme.RED]}
            className={cx("icon-loading")}
        />
    );

    const contentIcon = icon && (
        <Icon
            className={cx("icon", {
                icon_hidden: isLoading,
            })}
        >
            {icon}
        </Icon>
    );

    return (
        <>
            {loadingIcon}
            {contentIcon}
            <span
                className={cx("container", {
                    container_hidden: isLoading,
                })}
            >
                {content}
            </span>
        </>
    );
};
