import { CountryVersion } from "common/constants/enum";
import { languages } from "common/configs/languageConfig";

import france from "./france";
import italy from "./italy";
import portugal from "./portugal";
import spain from "./spain";
import english from "./english";
import uk from "./uk";

export default (version: CountryVersion) => ({
    [languages.en]: version === CountryVersion.UK ? uk : english,
    [languages.it]: italy,
    [languages.es]: spain,
    [languages.pt]: portugal,
    [languages.fr]: france,
});
