import { HTMLAttributes, ReactNode } from "react";

export interface HeadingProps extends HTMLAttributes<Record<string, never>> {
    children: ReactNode | string;

    tag?: HeadingTag;
    type?: HeadingType;
    theme?: HeadingTheme;
}

export enum HeadingTag {
    H1 = "h1",
    H2 = "h2",
    H3 = "h3",
    H4 = "h4",
    H5 = "h5",
    H6 = "h6",
}

export enum HeadingType {
    XL = "xl",
    M = "m",
    S = "s",
}

export enum HeadingTheme {
    Default = "default",
    Red = "red",
    White = "white",
    Black = "black",
}
