import { FC } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import { Text } from "common/components/Text";
import { Button, ButtonTheme } from "common/components/new/Button";
import { Container } from "common/components/Container";
import { Link } from "common/components/Link";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";

import { ErrorPropsItem, Site } from "../../content/types";
import styles from "./ErrorApi.module.scss";

const cx = classNames.bind(styles);

export const ErrorApi: FC<ErrorPropsItem & { site?: Site }> = ({
    site,
    title,
    subtitle,
    button,
}) => {
    return (
        <Container className={cx("container")}>
            <Link
                {...site}
                id={getAnalyticsId({
                    zone: "header",
                    element: GetAnalyticsIdElement.LINK,
                    label: "logo",
                })}
                className={cx("logo")}
            >
                <Icon>
                    <LogoIcon />
                </Icon>
            </Link>
            <Heading
                className={cx("title")}
                type={HeadingType.M}
                tag={HeadingTag.H3}
            >
                {title}
            </Heading>
            <Text className={cx("subtitle")}>{subtitle}</Text>
            {button?.text && (
                <Button
                    onClick={window.location.reload}
                    className={cx("button")}
                    theme={ButtonTheme.TRANSPARENT}
                >
                    {button.text}
                </Button>
            )}
        </Container>
    );
};
