import { FC } from "react";

import { BASE_URL, SITE_URLS } from "common/constants/enum";

import content from "./content";
import contacts from "./content/contacts";
import { Contact } from "./content/types";
import { ErrorApi } from "./components/ErrorApi";
import { ErrorNotFound } from "./components/ErrorNotFound";
import { ErrorReferral } from "./components/ErrorReferral";
import { ErrorLayoutProps, ErrorType } from "./ErrorLayout.types";

export const ErrorLayout: FC<ErrorLayoutProps> = ({
    type,
    version,
    language,
}) => {
    const contentByLanguage = content(version)[language];

    const site = {
        name: BASE_URL,
        link: SITE_URLS[version],
    };

    const contactsByVersion = contacts[version] as Contact[];

    switch (type) {
        case ErrorType.API:
            return (
                <ErrorApi {...contentByLanguage[ErrorType.API]} site={site} />
            );

        case ErrorType.REFERRAL:
        case ErrorType.ADVOCATE:
            return (
                <ErrorReferral
                    {...contentByLanguage[type]}
                    site={site}
                    contacts={contactsByVersion}
                />
            );

        default:
            return (
                <ErrorNotFound {...contentByLanguage[ErrorType.NOT_FOUND]} />
            );
    }
};
