import { FC } from "react";
import classNames from "classnames/bind";

import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Container } from "common/components/Container";
import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import { SocialMediaIcon } from "common/components/Icons/SocialMediaIcon";
import { Link } from "common/components/Link";

import { ErrorPropsReferralItem, Site, Contact } from "../../content/types";
import styles from "./ErrorReferral.module.scss";

const cx = classNames.bind(styles);

export const ErrorReferral: FC<
    ErrorPropsReferralItem & { site?: Site; contacts?: Contact[] }
> = ({ title, description, contacts, site }) => {
    return (
        <Container>
            <div className={cx("root")}>
                <Link
                    className={cx("logo")}
                    link={site?.link}
                    id={getAnalyticsId({
                        zone: "header",
                        element: GetAnalyticsIdElement.LINK,
                        label: "logo",
                    })}
                >
                    <Icon>
                        <LogoIcon />
                    </Icon>
                </Link>
                <Heading
                    className={cx("title")}
                    type={HeadingType.M}
                    tag={HeadingTag.H1}
                >
                    {title}
                </Heading>
                <Text className={cx("description")}>{description}</Text>
                {contacts && contacts.length > 0 && (
                    <ul className={cx("contacts-list")}>
                        {contacts.map((contact) => (
                            <li
                                className={cx("contacts-list__item")}
                                key={contact.title}
                            >
                                <Link
                                    className={cx("contacts-list__link")}
                                    link={contact.link}
                                >
                                    <Text className={cx("contacts-list__text")}>
                                        {contact.title}
                                    </Text>
                                    <Icon className={cx("contacts-list__icon")}>
                                        <SocialMediaIcon
                                            iconName={contact.icon}
                                        />
                                    </Icon>
                                </Link>
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        </Container>
    );
};
