import { ReactNode } from "react";

export const isContainsHtml = (str: string) => {
    return /<[a-z][\s\S]*>/i.test(str);
};

export const isStringWithHtml = (children: ReactNode | string): boolean => {
    if (typeof children !== "string") {
        return false;
    }

    return isContainsHtml(children);
};
