import { AppProps } from "next/app";
import { useState } from "react";
import { NextComponentType, NextPageContext } from "next";
import Head from "next/head";
import appWithI18n from "next-translate/appWithI18n";
import { PageProps } from "page.type";

import { ErrorLayout, ErrorType } from "common/components/ErrorLayout";
import { UtmService } from "common/services/UtmService";
import { CanonicalService } from "common/services/CanonicalService";
import { VisitorTrajectoryService } from "common/services/VisitorTrajectoryService";
import { CountryVersion, Language } from "common/constants/enum";
import { ErrorContext } from "common/context/errorContext";

import i18nConfig from "../i18n";

import "style/fonts.css";
import "style/styles.css";
import "style/variables.css";

interface MyAppProps extends AppProps<PageProps> {
    Component: NextComponentType<NextPageContext, unknown, PageProps>;
    pageProps: {
        title: string;
        language: string;
        pageConfig: string;
        description: string;
        contentFolder: string;
        version: string;
        locale: string;
    };
}

function MyApp({ Component, pageProps }: MyAppProps) {
    const {
        title,
        description,
        pageConfig,
        contentFolder,
        language,
        version,
        locale,
    } = pageProps;

    const [errorType, setErrorType] = useState<ErrorType | undefined>();

    return (
        <ErrorContext.Provider value={{ errorType, setErrorType }}>
            <UtmService />
            <VisitorTrajectoryService />
            <CanonicalService />
            <Head>
                {process.env.ENV === "production" && (
                    <>
                        {/* Google Tag Manager */}
                        <script
                            id="google-tag"
                            async
                            src="/scripts/google-tag.js"
                        ></script>
                        {/* End Google Tag Manager */}
                    </>
                )}
                {process.env.ENV === "production" && (
                    <>
                        {/* TikTok Pixel Code Start */}
                        <script
                            id="tiktok"
                            async
                            src="/scripts/tiktok.js"
                        ></script>
                        {/* TikTok Pixel Code End */}
                    </>
                )}
                {process.env.ENV === "production" &&
                    version === CountryVersion.FRANCE && (
                        <>
                            {/* Snap Pixel Code */}
                            <script
                                id="snapchat-pixel"
                                async
                                src="/scripts/snapchat-pixel.js"
                            ></script>
                            {/* End Snap Pixel Code */}
                        </>
                    )}
            </Head>
            {errorType ? (
                <ErrorLayout
                    type={errorType}
                    language={locale}
                    version={version as CountryVersion}
                />
            ) : (
                <Component
                    version={version as CountryVersion}
                    language={language as Language}
                    contentFolder={contentFolder}
                    pageConfig={pageConfig}
                    title={title}
                    description={description}
                />
            )}
        </ErrorContext.Provider>
    );
}

export default appWithI18n(MyApp as any, {
    ...i18nConfig,
    // Set to false if you want to load all the namespaces on _app.js getInitialProps
    skipInitialProps: true,
});
