import { FC } from "react";

import { ContactTypes, SocialTypes, IconTypes } from "common/constants/enum";
import PhoneIcon from "common/components/Icons/icons/24/phone.svg";
import WhatsappIcon from "common/components/Icons/icons/24/whatsapp.svg";
import MailIcon from "common/components/Icons/icons/24/mail.svg";
import FacebookIcon from "common/components/Icons/icons/24/facebook.svg";
import InstagramIcon from "common/components/Icons/icons/24/instagram.svg";
import TiktokIcon from "common/components/Icons/icons/24/tiktok.svg";
import RightIcon from "common/components/Icons/icons/24/right.svg";

export const SocialMediaIcon: FC<{
    iconName?: ContactTypes | SocialTypes | IconTypes;
}> = ({ iconName }) => {
    switch (iconName) {
        case ContactTypes.PHONE:
            return <PhoneIcon />;
        case SocialTypes.WA:
            return <WhatsappIcon />;
        case ContactTypes.EMAIL:
            return <MailIcon />;
        case SocialTypes.FB:
            return <FacebookIcon />;
        case SocialTypes.INSTAGRAM:
            return <InstagramIcon />;
        case SocialTypes.TIKTOK:
            return <TiktokIcon />;
        default:
            return <RightIcon />;
    }
};
