import { ButtonHTMLAttributes, ReactNode, RefObject } from "react";

import { ColorsStroke } from "common/components/Icons/LoadingIcon/types";
import { CountryVersion } from "common/constants/enum";

export enum ButtonSize {
    S = "small",
    M = "medium",
}

export enum ButtonTheme {
    RED = "red",
    BORDERED_TRANSPARENT = "bordered-transparent",
    TRANSPARENT = "transparent",
    LIGHT = "light",
    GERMANY = "germany",
}

export const mapThemeColorLoading = {
    [ButtonTheme.RED]: ColorsStroke.WHITE,
    [ButtonTheme.BORDERED_TRANSPARENT]: ColorsStroke.DARK_GRAY,
    [ButtonTheme.TRANSPARENT]: ColorsStroke.BLUE,
    [ButtonTheme.GERMANY]: ColorsStroke.DARK_GRAY,
    [ButtonTheme.LIGHT]: ColorsStroke.DARK_GRAY,
};

// @ts-nocheck
export interface ButtonProps extends ButtonHTMLAttributes<unknown> {
    link?: string;
    href?: string;
    theme?: ButtonTheme;
    size?: ButtonSize;
    isLoading?: boolean;
    target?: string;
    ref?: RefObject<HTMLButtonElement>;
    classNameContainer?: string;
    icon?: ReactNode;
    isMaxWidth?: boolean;
    version?: CountryVersion;
}

export interface ContentProps {
    content: string | ReactNode;

    theme?: ButtonTheme;
    isLoading?: boolean;
    icon?: ReactNode;
}
